.main-form {
    margin-bottom: 30px;

    &__txt {
        color: $fontl;
        line-height: 1.4;
        margin-bottom: 20px;
    }

    &__btn {
        padding: 20px 30px;
    }
}

.form-group_modal--main-form {
    padding: 0 0 0 30px;

    .form_input__modal {
        background: #F6F6F6;
        height: 67px;
        border: none;
        padding-left: 20px;

        &:active {
            border: none;
        }

        &:focus {
            border: none;
        }
    }

    .main-form__input {
        display: flex;
        flex-direction: column;
    }

    .span-main-form {
        font-size: 16px;
        color: $fontd;
        margin-bottom: 10px;

        span {
            color: red;
        }
    }
}

.cresults__agreement {
    margin: 20px 0;
}

.checkbox-text {
    margin-left: 10px;
    font-size: 13px;
    color: $fontl;

    a {
        color: $fontd;
        text-decoration: underline;
    }
}

@media only screen and (max-width : 1200px) {

    .form-group_modal--main-form {
        padding: 0 0 0 0;

    }
}
