.c-btn {
    display: inline-flex;
    justify-content: center;
    align-items: center;
    border-radius: 4px;
    cursor: pointer;
    text-decoration: none;
    border: none;

}

.btn-blue {
    background: $blue;
    color: #fff;


    &:hover {
        background: darken($blue, 5%);
        color: #fff;

    }
}

.btn-gray {
    background: #F4F4F4;
    color: $fontl;
    padding: 12px 18px;
    display: inline-flex;
    align-items: center;

    &:hover {
        background: darken(#F4F4F4, 5%);
        color: $fontd;
    }

    i {
        font-size: 14px;
        color: $fontl;
        margin-left: 10px;
        margin-top: 3px;
    }
}

@media only screen and (max-width : 576px) {
    .btn-gray {
        padding: 7px 5px;
        font-size: 12px;



        i {
            font-size: 14px;
            color: $fontl;
            margin-left: 10px;
            margin-top: 3px;
        }
    }

}
