.person {
    &__top {
        display: flex;
        justify-content: space-between;
        flex-wrap: wrap;
    }

    &__btn {
        padding: 20px 40px;
    }

    &__bottom {
        display: flex;
        margin-bottom: 30px;
        flex-wrap: wrap;
    }

    &__phone {
        margin-right: 40px;
    }

    &__txt {
        margin-bottom: 20px;
    }


}

.person-title {
    font-style: normal;
    font-weight: 400;
    font-size: 18px;
    line-height: 1.3;
    display: block;
    color: #66727F;
    margin-bottom: 7px;
}

.person-info {
    display: block;
    font-style: normal;
    font-weight: 400;
    font-size: 24px;
    color: #0B1F33;
    line-height: 1.3;
}

@media only screen and (max-width : 576px) {
    .person {
        &__top {
            margin: 30px 0;
        }

        &__bottom {
            margin: 30px 0;
        }

        &__phone {
            margin-bottom: 10px;
        }

        &__post {
            margin-bottom: 10px;
        }
    }

    .person-title {

        font-size: 16px;

    }

    .person-info {

        font-size: 18px;

    }
}
