.header {
    width: 100%;


    hr {
        margin: 0;
        padding: 0;
    }

    &__top {
        display: flex;
        justify-content: space-between;
        align-items: center;
        padding: 10px 0;
    }

    &__wrp {
        display: flex;
        margin-right: 20px;
    }

    &__ic {
        width: 15px;
        height: 15px;
        fill: $fontd;
        margin-right: 6px;
        display: inline-block;
        margin-bottom: 6px;
    }

    &__info {
        display: flex;
        align-items: center;
    }

    &__btn {
        padding: 20px 30px;
        font-size: 16px;
    }

    &__tel {
        text-align: end;

        a {
            font-style: normal;
            font-weight: 700;
            font-size: 20px;
            color: $fontd;
            margin-bottom: 10px;
            display: inline-block;

        }
    }

    &__tel-info {
        font-style: normal;
        font-weight: 400;
        font-size: 14px;
        color: $fontl;
    }

}

@media only screen and (max-width : 1200px) {
    .header {
        &__btn {
            padding: 13px 17px;
            font-size: 14px;
        }

        &__tel {
            a {
                font-size: 18px;
                color: $fontd;
                margin-bottom: 5px;


            }
        }

        &__tel-info {
            font-size: 12px;

        }
    }
}

@media only screen and (max-width : 920px) {
    .header {
        &__info {
            display: none;
        }
    }

    .menu_wrp {
        display: flex;
        justify-content: space-between;
        align-items: center;
    }
}
