.map_wrp {
    margin: 40px 0;
}


#map {
    width: 100%;
    height: 600px;
    display: block;

}

.ballon-ttl {
    font-size: 14px;
    font-weight: 700;
    color: $fontd;
    margin-bottom: 10px;
}

.ballon-more {
    font-size: 12px;
    line-height: 1.3;
    color: $fontl;
    margin-bottom: 20px;
}

.ballon-img {
    max-width: 120px;
    height: auto;
}

@media only screen and (max-width : 576px) {
    .map_wrp {
        margin: 30px 0;
    }
}
