//прижимаем футер

.page {
    display: flex;
    flex-direction: column;
    min-height: 100vh;
}



.footer {
    margin-top: auto;
    background: #333333;
    padding: 40px 0;

    hr {
        background-color: #6f6f6f;
    }

    &__logo {
        margin-bottom: 40px;
    }

    form {
        display: flex;
        margin-bottom: 40px;

    }

    &__input {
        height: 61px;
        position: relative;
        background: #474747;
        padding: 7px;
        border: none;
        color: #fff;
        z-index: 30;
        width: 70%;

        &::placeholder {
            color: #fff;
        }
    }

    &__btn {
        background: transparent;
        border: none;
        margin-left: -50px;
        position: relative;
        z-index: 40;
        opacity: .8;
        transition: all .3s;

        &:hover {
            opacity: 1;
        }
    }

    &__ic {
        height: 25px;
        width: 25px;
        fill: #fff;
    }

    &__heading {
        font-style: normal;
        font-weight: 700;
        font-size: 18px;
        line-height: 1.3;
        color: #FFFFFF;
        margin-bottom: 30px;
        text-transform: uppercase;
    }

    &__menu {
        margin-bottom: 20px;
    }

    &__menu li {
        line-height: 2.1;

    }

    &__menu li a {
        color: #fff;
        opacity: .7;

        &:hover {
            opacity: 1;
        }
    }

    &__privacy {
        margin: 15px 0 15px 0;
        color: #fff;
        opacity: .4;
        font-size: 12px;
        line-height: 1.3;
    }

}

@media only screen and (max-width : 576px) {
    .footer {
        &__input {
            width: 100%;
        }
    }

}
