.sticky-top {
    top: 110px;
}

.sidebar-nav {
    &__title {
        font-style: normal;
        font-weight: 700;
        font-size: 18px;
        color: $fontd;
        line-height: 1.3;
        margin: 10px 0;
    }

    li {
        border-bottom: 1px solid rgba(236, 236, 236, 1);
    }

    .active {
        a {
            font-weight: 600;
            color: $fontd;
        }
    }

    a {
        padding: 17px 0 17px 15px;
        display: block;
        font-weight: 400;
        color: $fontl;
        line-height: 1.3;

        &:hover {
            color: $fontd;
        }
    }
}
