.s-objects {
    margin: 40px 0;
}

.slider-objects,
.slider-objects2 {
    margin: 0 -5px;
}

.object-item {
    position: relative;
    border: 1px solid #ececec;
    margin: 5px;
    display: block;

    &__img {
        height: 280px;
        overflow: hidden;
        position: relative;
        display: block;
        margin-bottom: 20px;
        width: 100%;

        img {
            height: 100%;
            width: 100%;
            object-fit: cover;
        }

    }

    &__wrp {
        display: flex;
        align-items: center;
        justify-content: space-between;
        margin-bottom: 20px;
        padding: 0 15px;
    }

    &__builder {
        font-style: normal;
        font-weight: 400;
        font-size: 14px;
        color: $fontl;

        line-height: 1.3;

    }

    &__status {
        font-size: 11px;
        color: #fff;
        padding: 7px 12px;
        display: flex;
        align-items: center;
        justify-content: center;
        border-radius: 4px;
    }

    &__status--green {
        background: #0EA61D;
    }

    &__status--blue {
        background: $blue;
    }

    &__title {
        font-weight: 700;
        font-size: 18px;
        line-height: 1.3;
        color: $fontd;
        display: block;
        margin: 0;
        margin-bottom: 20px;
        padding: 0 15px;
        overflow: hidden;
    }

    &__place {
        padding: 0 15px;
        font-weight: 400;
        font-size: 18px;
        line-height: 1.3;
        color: $fontd;
        margin-bottom: 30px;
    }

    &__btn {
        display: inline-block;
        padding: 20px 25px;
        margin: 20px 15px;
        margin-top: auto;
    }
}

.collect__arrows,
.collect__arrows2 {
    margin-bottom: 20px;
    position: relative;
    z-index: 600;
}

.collect__arrows .pr-prev1,
.collect__arrows2 .pr-prev2 {
    width: 103px;
    height: 46px;
    background: #F2EDEA;
    font-size: 20px;
    color: #66727F;
    border: none;
    border-radius: 4px;
    margin-right: 10px;
}

.collect__arrows .pr-next1,
.collect__arrows2 .pr-next2 {
    width: 103px;
    height: 46px;
    background: #F2EDEA;
    font-size: 20px;
    color: #66727F;
    border: none;
    border-radius: 4px;
}


@media only screen and (max-width : 768px) {
    .news-item {
        &__title {
            height: auto;

        }

    }

}
