.news-single {
    img {
        width: 100%;
        margin-bottom: 20px;
    }

    &__date {
        font-style: normal;
        font-weight: 400;
        font-size: 14px;
        color: $fontl;
        margin-bottom: 20px;

    }

    &__title {
        font-weight: 700;
        font-size: 18px;
        line-height: 1.3;
        color: $fontd;
        display: block;
        margin: 0;
        margin-bottom: 20px;
    }

    &__content {
        margin-bottom: 40px;
    }
}
