.content {
    padding: 30px;
    background: #FFFFFF;
    box-shadow: 0px 15px 20px rgba(0, 0, 0, 0.06);
    margin-top: -60px;
    margin-bottom: 30px;

    &__h2 {
        font-weight: 700;
        font-size: 32px;
        color: $fontd;
        line-height: 1.3;
        margin: 15px 0 7px 0;
    }

    &__btn {
        padding: 20px 40px;
        margin-top: 30px
    }

    p {
        margin: 7px 0;
        color: $fontl;
        line-height: 1.4;
        font-size: 18px
    }

    .main-text {
        padding: 15px 15px 15px 40px;
        background: #F5F5F5;
        border-left: 3px solid $blue;
        margin: 7px 0;
    }
}

@media only screen and (max-width : 576px) {
    .content {
        padding: 0;
        background: #FFFFFF;
        box-shadow: none;
        margin-top: 0;

        &__h2 {
            font-size: 24px;
        }

        p {
            font-size: 16px;
        }
    }


}
