.gallery {
    margin-bottom: 25px;
    height: 220px;
    display: block;

    img {
        height: 100%;
        width: 100%;
        object-fit: cover;
    }
}

.nav-pills .nav-link.active,
.nav-pills .show > .nav-link {
    color: #fff;
    background-color: $blue;
    padding: 20px 30px
}

.nav-pills .nav-link {
    background: 0 0;
    background-color: rgba(0, 0, 0, 0);
    border: 0;
    border-radius: 4px;
}

.nav-pills .nav-link {
    background: 0 0;
    background-color: rgba(0, 0, 0, 0);
    background-color: rgba(0, 0, 0, 0);
    border: 0;
    border-radius: 4px;
}

.nav-pills .nav-link {
    background: 0 0;
    border: 0;
    border-radius: 0.25rem;
    padding: 20px 30px
}

.nav-link:focus,
.nav-link:hover {
    color: $fontl;
}

.video-block {
    margin-bottom: 20px;
}

@media only screen and (max-width : 576px) {
    .gallery {

        height: 120px;
    }

    .nav-pills {
        margin-top: 10px;
    }
}
