.news {
    margin: 40px 0;
}

.documents {
    background: #FAFCFF;
    padding: 30px 0;
}

.block-title-wrp {
    display: flex;
    align-items: center;
    justify-content: space-between;
    margin-bottom: 20px
}

.banner-shine {

    overflow: hidden;
    position: relative;


    &:hover::before {
        transition: all 550ms cubic-bezier(0.19, 1, 0.22, 1);
        left: 120%;


    }

    &::before {
        position: absolute;
        top: 0;
        left: -100%;
        z-index: 2;
        display: block;
        content: "";
        width: 25%;
        height: 100%;
        background: linear-gradient(to right, rgba(255, 255, 255, 0) 0, rgba(255, 255, 255, 0.3) 100%);
        transform: skewX(-25deg);
        transition: all 550ms cubic-bezier(0.19, 1, 0.22, 1);


    }
}

.news-item {
    position: relative;
    border: 1px solid #ececec;
    width: 100%;
    margin-bottom: 20px;



    &__img {
        height: 280px;
        overflow: hidden;
        position: relative;
        display: block;
        margin-bottom: 20px;
        width: 100%;

        img {
            height: 100%;
            width: 100%;
            object-fit: cover;
        }

    }

    &__date {
        font-style: normal;
        font-weight: 400;
        font-size: 14px;
        color: $fontl;
        margin-bottom: 20px;
        padding: 0 15px;
    }

    &__title {
        font-weight: 700;
        font-size: 18px;
        line-height: 1.3;
        color: $fontd;
        display: block;
        margin: 0;
        margin-bottom: 20px;
        padding: 0 15px;
        height: 70px;
        overflow: hidden;
    }

    &__btn {
        display: inline-block;
        padding: 20px 25px;
        margin: 20px 15px;
        margin-top: auto;
    }
}

@media only screen and (max-width : 768px) {
    .news-item {
        &__title {
            height: auto;

        }

    }

}
