.custom-checkbox {
    display: flex;
    width: 100%;
    margin: 0;
    cursor: pointer;
    flex-direction: row;
    align-items: center;
}

.checkbox-hidden {
    display: none;
}

.checkbox-wrap {
    display: flex;
    align-items: center;
    justify-content: center;
    width: 20px;
    max-width: 20px;
    flex: 0 0 20px;
    height: 20px;
    border-radius: 2px;
    border: 2px solid $blue;
    background-color: $blue;
}

.custom-checkbox input:checked + .checkbox-wrap::after {
    transform: scale(1);
    opacity: 1;
}

.custom-checkbox .checkbox-wrap::after {
    content: "";
    display: block;
    width: 15px;
    height: 13px;
    background: url(../img/svg-check-ic.svg) no-repeat 0 0;

    background-size: 100%;
    will-change: transform;
    transform: scale(0);
    opacity: 0;
    transition: all 0.1s linear;
}

/*Радио кнопка*/
.input-hidden {
    display: none;
}

.ui-radiobox {
    display: block;
    margin-bottom: 0;
    cursor: pointer;
}

.ui-radiobox_custom {
    font-weight: 300;
    font-size: 14px;
    color: #747474;
}

.ui-radiobox__label {
    display: flex;
    align-items: center;
    position: relative;
    line-height: 1.4;
}

ui-radiobox__label::before {
    width: 27px;
    flex: 0 0 27px;
    height: 27px;
    margin-right: 16px;
    background: #fff;
    border: 2px solid #e7e7e7;
}

.ui-radiobox__label::before,
.ui-radiobox__label::after {
    content: "";
    display: block;
    border-radius: 50%;
}

.ui-radiobox input:checked + .ui-radiobox__label::after {
    transform: scale(1);
}

.ui-radiobox__label::after {
    width: 13px;
    height: 13px;
    position: absolute;
    top: 7px;
    left: 7px;
    background: #1E1D2B;
    will-change: transform;
    transform: scale(0);
    transition: all 0.1s linear;
}
