.doc {
    padding: 20px;
    border: 1px solid #ececec;
    width: 100%;
    margin-bottom: 20px;
    background: #fff;

    &__item {
        display: flex;


        img {
            width: 32px;
            height: 32px;
            flex-shrink: 0;
            margin-right: 20px;
        }
    }

    &__title {
        font-style: normal;
        font-weight: 400;
        font-size: 16px;
        line-height: 1.3;
        color: $fontd;
    }
}
