.heading-main {
    background: url('../img/bg1.jpg');
    background-repeat: no-repeat;
    background-size: cover;




    &__h1 {
        font-family: 'Play', sans-serif;
        font-style: normal;
        font-weight: 400;
        font-size: 40px;
        line-height: 1.4;
        color: #FFFFFF;
        padding: 50px 0;

        span {
            font-weight: 700;
        }
    }

    .relative {
        position: relative;

    }


    &__img img {
        position: absolute;
        right: 0;
        bottom: 0;
        display: block;
    }



}

@media only screen and (max-width : 768px) {
    .heading-main {
        &__img img {
            position: relative;
        }
    }

}

@media only screen and (max-width : 576px) {
    .heading-main {
        background: url('../img/bg1.jpg');
        background-repeat: no-repeat;
        background-size: cover;




        &__h1 {
            font-size: 24px;
            line-height: 1.4;
            padding: 30px 0;
        }

    }
}
