.aside {
    position: fixed;
    right: 0px;
    top: 204px;
    background-color: #fff;
    box-shadow: -7px 5px 11px rgb(0 0 0 / 10%);
    border-radius: 4px 0 0 4px;
    overflow: hidden;
    z-index: 900;
    display: flex;
    flex-direction: column;

    .aside-block {
        width: 65px;
        height: 65px;
        display: flex;
        align-items: center;
        justify-content: center;
        border-bottom: 1px solid #ececec;
        background-color: #fff;
        cursor: pointer;

        &:last-child {
            border-bottom: 0;
        }

        &:hover {
            background-color: #ececec;
        }
    }

    &__ic {
        width: 19px;
        height: 19px;
        fill: $blue;
    }

    .aside1 {
        border-bottom: 1px solid #ececec;
    }
}

@media only screen and (max-width : 990px) {
    .aside {
        display: none;
    }
}
