.myfilter {
    padding: 20px 10px;
    box-shadow: 0 5px 15px #ececec;
}

.filter_title {
    font-size: 22px;
    font-weight: 700;
    margin-bottom: 30px
}

.filters__label {
    font-size: 18px;
    font-weight: 700;
    padding: 20px 0;
    border-bottom: 1px solid #ececec;
}

.filters-row {
    padding: 15px 5px 15px 10px;
    background: #f9f9f9;
}

.ui-radiobox__label {
    display: flex;
    align-items: center;
    position: relative;
    line-height: 1.4;



}

.ui-radiobox__label::before,
.ui-radiobox__label::after {
    content: '';
    display: block;
    border-radius: 50%;
}

.ui-radiobox__label::before {
    width: 27px;
    flex: 0 0 27px;
    height: 27px;
    margin-right: 16px;
    background: #fff;
    border: 2px solid #e7e7e7;
}

.ui-radiobox__label::after {
    width: 13px;
    height: 13px;
    position: absolute;
    top: 7px;
    left: 7px;
    background: $blue;
    will-change: transform;
    transform: scale(0);
    transition: all 0.1s linear;
}

.ui-radiobox input:checked + .ui-radiobox__label::after {
    transform: scale(1);
}

.ui-radiobox:not(:last-child) {
    margin-bottom: 13px;
}

.input-hidden {
    display: none;
}

.filters-check {

    display: flex;
    width: 100%;
    margin: 0;
    cursor: pointer;

    &:not(:last-child) {
        margin-bottom: 15px;
    }

    .checkbox-wrap {

        display: flex;

        align-items: center;

        justify-content: center;
        width: 20px;
        max-width: 20px;

        flex: 0 0 20px;
        height: 20px;
        border-radius: 2px;
        border: 2px solid #e7e7e7;

        &::after {
            content: '';
            display: block;
            width: 12px;
            height: 10px;
            background: url("../img/svg-check-ic.svg") no-repeat 0 0;
            background-size: 100%;
            will-change: transform;

            transform: scale(0);
            opacity: 0;

            transition: all 0.1s linear;
        }
    }

    .checkbox-text {
        display: inline-block;
        margin-top: 1px;
        margin-left: 14px;
        font-size: 14px;
    }

    input {
        &:checked {
            + {
                .checkbox-wrap {
                    &::after {

                        transform: scale(1);
                        opacity: 1;
                    }
                }
            }
        }
    }
}

.js-accordion-btn {
    cursor: pointer;
    position: relative;
    padding-left: 20px;

    &::before {
        content: '';
        display: block;
        width: 5px;
        height: 9px;
        position: absolute;
        right: 2px;
        top: 50%;
        margin-top: -4px;
        z-index: 0;
        background: url("../img/ic-arrow.svg") no-repeat;
        background-size: 100%;
        transform: rotate(90deg);

        transition: all 0.1s linear;
    }
}

.js-accordion-btn.active {
    &::before {

        transform: rotate(0deg);
    }
}
